* {
  box-sizing: border-box;
}

body {
  letter-spacing: .04em;
  margin: 0;
  padding: 0;
  font-family: Montserrat;
  line-height: 1.14;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
  padding: 0;
}

.list {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.section {
  padding: 20px 24px;
}

.go-top {
  cursor: pointer;
  fill: #fff;
  visibility: hidden;
  opacity: 0;
  z-index: 101;
  background-color: #3a3636;
  border: 10px solid #363434;
  border-radius: 50%;
  transition: opacity .5s, visibility .5s;
  display: flex;
  position: fixed;
  bottom: 10px;
  right: 10px;
}

.go-top:hover, .go-top:focus {
  filter: brightness(1.5);
  transition: all .2s;
}

.go-top-show {
  opacity: 1;
  visibility: visible;
  z-index: 100;
  transition: opacity .5s, visibility .5s;
}

.container {
  max-width: 767px;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 1024px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1280px;
  }
}

.button {
  text-align: center;
  cursor: pointer;
  min-width: 236px;
  color: wheat;
  background-color: rgba(35, 44, 54, .4);
  border: 2px solid rgba(0, 0, 0, .4);
  border-radius: 25px;
  margin-left: auto;
  margin-right: auto;
  padding: 16px 38px;
  font-family: inherit;
  font-size: 18px;
  font-weight: 700;
  display: block;
}

.hero__backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .4s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.hero__backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.hero__backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(0);
}

.modal {
  height: 100%;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  display: flex;
}

.modal__container {
  max-width: 400px;
  width: 100%;
  max-height: 609px;
  height: 100%;
  z-index: 2;
  background-color: #fff;
  border-radius: 25px;
  padding: 40px;
  position: relative;
  overflow-y: auto;
}

@media screen and (min-width: 1200px) {
  .modal__container {
    max-width: 528px;
    max-height: 581px;
  }
}

.modal__title {
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 24px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.17;
}

.modal__text-data {
  text-align: center;
  margin-bottom: 14px;
  font-size: 12px;
}

.button__close {
  width: 35px;
  height: 35px;
  fill: wheat;
  cursor: pointer;
  background-color: gray;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: transfom .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
  position: absolute;
  top: 16px;
  right: 16px;
  transform: scale(1);
}

.button__close:hover, .button__close:focus {
  fill: #00f;
}

.hero__backdrop.is-hidden .button__close {
  transform: scale(0);
}

.modal__label {
  letter-spacing: .01em;
  color: gray;
  margin-bottom: 8px;
  font-size: 12px;
  line-height: 1.17;
  display: block;
}

.modal__field {
  margin-bottom: 10px;
  position: relative;
}

.modal__input {
  width: 100%;
  height: 40px;
  border: 1px solid #000;
  border-radius: 4px;
  padding: 0 42px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.modal__input:focus-within {
  border-color: #00f;
  outline: none;
}

.modal__input:focus-within + .modal__icon {
  fill: #00f;
}

.modal__icon {
  fill: #000;
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
}

.modal__coment {
  resize: none;
  width: 100%;
  height: 120px;
  border: 1px solid gray;
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 12px 16px;
  transition: border-color .25s cubic-bezier(.4, 0, .2, 1);
}

.modal__coment:focus-within {
  border-color: #00f;
  outline: none;
}

.modal__coment::-ms-input-placeholder {
  letter-spacing: .01em;
  color: gray;
  font-size: 12px;
  line-height: 1.17;
}

.modal__coment::placeholder {
  letter-spacing: .01em;
  color: gray;
  font-size: 12px;
  line-height: 1.17;
}

.checkbox__field {
  align-items: center;
  display: flex;
}

.checkbox__label {
  padding: 8px;
  font-size: 12px;
  position: relative;
}

.checkbox__link {
  color: #00f;
}

.modal__button {
  margin: 0 auto;
  padding: 10px 52px;
  display: block;
}

.modal__text_pay {
  text-align: center;
  margin-top: 20px;
  font-size: 12px;
}

.modal__package_button {
  margin: 40px auto 12px;
  padding: 30px;
  display: block;
}

@media (max-width: 767px) {
  .header__menu {
    display: none;
  }
}

@media (min-width: 768px) {
  .header__menu {
    gap: 30px;
    display: flex;
  }
}

.menu__button {
  min-height: 40px;
  min-width: 40px;
  fill: wheat;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  display: flex;
  position: absolute;
  top: 16px;
  right: 16px;
}

@media (min-width: 768px) {
  .menu__button {
    display: none;
  }
}

.menu__button:hover, .menu__button:focus {
  background-color: rgba(0, 0, 0, .1);
}

.menu-container {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  background-color: #595c6e;
  padding: 48px 40px;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.menu-container .menu__button {
  color: wheat;
  position: absolute;
  top: 16px;
  right: 16px;
}

.header__menu_mobile {
  flex-direction: column;
